:root {
  --primary-color: #a5005b;
  --secondary-color: #860000;
  --complimentary-color: #007963;
  --contrast-color: #a19700;
  --light-color: #7c0092;
  }
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  background: --var var(--primary-color);
}

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.video-container {
  position: relative;
  display: flex;
  width: 100vw;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 50px); /* Reduce height to account for the tab menu */
  overflow: hidden;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 1;
  transform: scale(1);
}

video {
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}

img {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}

.tab-menu {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  background: var(--secondary-color);
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 16px;
  text-decoration: none;
  padding: 10px;
  text-align: center;
}

.tab {
  flex: 1;
  text-align: center;
  padding: 0px 0;
  cursor: none;
  border-radius: 12px;
  transition: all ease-in .5s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tab.active {
  font-size: 24px;
  color: #fff;
  text-decoration: none;
  padding: 15px;
  text-align: center;
  background-color: var(--primary-color);
}

/* General styles for the content container */
/* .video-container {
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 1;
  transform: scale(1);
} */

/* Custom transition for Tab 0 */
.tab-0 {
  background-color:var(--primary-color); /* Example color */
}

.tab-0.content-container {
  transform: translateX(0);
  opacity: 1;
}

/* Custom transition for Tab 1 */
.tab-1 {
  background-color: var(--secondary-color);
}

.tab-1.content-container {
  transform: translateX(-100%);
  opacity: 0.8;
}

/* Custom transition for Tab 2 */
.tab-2 {
  background-color: var(--complimentary-color);
}

.tab-2.content-container {
  transform: translateX(100%);
  opacity: 0.6;
}

/* Add this to your App.css */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.video-container.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}